* {
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

#navbar {

  width: 100%;
  height: 60px;
  background-color: rgb(245, 245, 245);
}
#navbar nav ul {
  width: 80%;
  height: 60px;
  /* margin: 0 auto; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#navbar nav ul li {
  list-style: none;
  justify-content: center;
  text-decoration: none;
  text-align: center;

}

#navbar nav ul li a {
  text-align: center;
  justify-content: center;
  color: rgb(92, 92, 92);
  font-size: 25px;
  margin: 50;
  padding: 50;
  font-weight: bold;
}

#formBlock {
  width: 100%;
  height: 90vh;
}
#formBlock article {
  /* background-color: gray; */
  width: 40%;
  height: 50rem;
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
}
#formBlock article form {
  background-color: rgb(245, 245, 245);
  width: 1000px;
  padding: 10px 10px;
  border-radius: 3px;
}
#formBlock article form label {
  display: block;
  color: rgb(90, 90, 90);
  font-weight: bold;
  font-size: 20px;
  padding: 5px 0px;
}
#formBlock article form input {
  width: 100%;
  height: 30px;
  border-radius: 3px;
  border: none;
  padding-left: 10px;
}
#formBlock article form select {
  width: 100%;
  height: 30px;
  border-radius: 3px;
  border: none;
}
#formBlock button {
  background-color: rgb(38, 95, 216);
  color: rgb(245, 245, 245);
  padding: 10px 10px;
  border: none;
  border-radius: 3px;
  border: 1px solid #fff;
  font-weight: bold;
  font-size: 15px;
  justify-content: center;
  display: flex;
  width: 100%;
  cursor: pointer;
  margin-top: 10px;
}
#formBlock button:hover {
  background-color: #fff;
  color: grey;
}
